import { useState, useEffect, useCallback } from 'react';
import FireBaseConexion from '../../../Services/FireBase';

const fireBaseConexion = new FireBaseConexion();

export function useProyectos() {
    const [dataProyectos, setDataProyectos] = useState([]);
    const [visibleCount, setVisibleCount] = useState(4);

    // Funcin para cargar ms proyectos
    const loadMoreProjects = () => {
        setVisibleCount(prevCount => Math.min(prevCount + 2, dataProyectos.length));
    };

    // Funcin para cargar menos proyectos
    const loadLessProjects = () => {
        setVisibleCount(prevCount => Math.max(prevCount - 2, 4)); // Mantiene al menos 4 visibles en pantallas grandes
    };

    // Funcin para detectar el ancho de la pantalla y ajustar visibleCount
    const updateVisibleCountBasedOnScreenSize = useCallback(() => {
        if (window.innerWidth < 480) {
            setVisibleCount(2); // Muestra 2 proyectos en pantallas menores a 480px
        } else {
            setVisibleCount(4); // Muestra 4 proyectos en pantallas ms grandes
        }
    }, []);

    // Llama a la funcin fetch para obtener los documentos de Firebase
    const fetchDocuments = useCallback(async (collectionName) => {
        try {
            const proyectos = await fireBaseConexion.getFireStore(collectionName);
            setDataProyectos(proyectos);
        } catch (error) {
            console.error('Error fetching documents:', error);
        }
    }, []);

    useEffect(() => {
        fetchDocuments('Proyectos');

        // Ejecuta la funcin para ajustar el visibleCount en el primer renderizado
        updateVisibleCountBasedOnScreenSize();

        // Agrega el event listener para ajustar el visibleCount al cambiar el tamao de la pantalla
        window.addEventListener('resize', updateVisibleCountBasedOnScreenSize);

        // Limpia el event listener cuando el componente se desmonta
        return () => window.removeEventListener('resize', updateVisibleCountBasedOnScreenSize);
    }, [fetchDocuments, updateVisibleCountBasedOnScreenSize]);

    return {
        dataProyectos,
        visibleCount,
        loadMoreProjects,
        loadLessProjects
    };
}

export default useProyectos;
